@import "./card.css";
@import "./star.css";

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  color: var(--second-text-color);
  font-weight: 400;

  --main-text-color: #060b35;
  --second-text-color: #616070;
  --button-color: #00acdc;
}

body,
h2,
p {
  margin: 0;
  padding: 0;
}
